<template lang="pug">
    .pd20
        <div class="navigate-bar">
            <span>供应商审核</span>
        </div>
        el-form.table-search-form(size="medium" :model="searchForm" :inline="true")
            el-form-item(label="审核状态 ：" )
                <el-radio-group v-model="searchForm.approved" @change="reloadTable">
                    <el-radio-button label="">全部</el-radio-button>
                    <el-radio-button label="0">待审核</el-radio-button>
                    <el-radio-button label="1">审核通过</el-radio-button>
                    <el-radio-button label="-1">审核失败</el-radio-button>
                </el-radio-group>
        gh-table(ref="ghTable" url="/company/list" :searchForm="searchForm")
            <el-table-column prop="name" label="供应商企业名称" />
            <el-table-column prop="creditCode" label="统一社会信用代码" width="180"/>
            <el-table-column prop="contactType" label="对接类型" >
                <div slot-scope="scope">
                    // <span v-if="scope.row.contactType == 1" type="warning">设备对接</span>
                    <span v-if="scope.row.contactType == 2" type="success">系统对接</span>
                    <span v-if="scope.row.contactType == 3" type="danger">软硬件适配</span>
                </div>
            </el-table-column>
            <el-table-column prop="username" label="用户名" />
            <el-table-column prop="password" label="密码" />
            <el-table-column prop="businessContact" label="业务联系人" />
            <el-table-column prop="businessContactPhone" label="业务联系人电话" />
            <el-table-column prop="ctime" label="发布日期" />
            <el-table-column prop="approved" label="状态">
                <div slot-scope="scope">
                    <el-tag v-if="scope.row.approved == 0" type="warning">待审核</el-tag>
                    <el-tag v-if="scope.row.approved == 1" type="success">审核通过</el-tag>
                    <el-tag v-if="scope.row.approved == -1" type="danger">审核失败</el-tag>
                </div>
            </el-table-column>
            <el-table-column prop="address" label="操作" width="280" fixed="right">
                <div class="table-cell-ctrl" slot-scope="scope">
                    <router-link :to="{path:'/examDetail',query:{uuid:scope.row.uuid}}">详情</router-link>
                    // <router-link :to="{path:'/examination',query:{uuid:scope.row.uuid}}">日志</router-link>
                    <a v-if="+scope.row.approved !== 1 " @click="examCompany(scope.row.uuid,scope.row.name,1)">通过</a>
                    <a v-if="+scope.row.approved !== -1 " @click="examCompany(scope.row.uuid,scope.row.name,-1)">拒绝</a>
                    <a @click="delCompany(scope.row.uuid,scope.row.name)">删除</a>
                </div>
            </el-table-column>

        el-dialog(
            :title="dialog.approved == 1 ? '审核通过' : '审核拒绝'" center
            append-to-body :visible.sync="dialog.show" width="500px")
            .dialog-tip
                | 确定{{dialog.approved == 1 ? '通过' : '拒绝'}}
                span.dialog-tip-name {{dialog.companyName}}
                | 的审核？
            el-form(label-position="left" label-width="50px"  :model="dialog.form" ref="form")
                <!--el-form-item(label="账户" v-if="dialog.approved == 1")-->
                    <!--el-input(v-model="dialog.form.account")-->
                <!--el-form-item(label="密钥" v-if="dialog.approved == 1")-->
                    <!--el-input(v-model="dialog.form.secretKey")-->
                el-form-item(label="备注" v-if="dialog.approved !== 1")
                    el-input(type="textarea" :rows="3" v-model="dialog.form.note")
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialog.show = false">取 消</el-button>
                <el-button type="primary" @click="examSubmit">确 定</el-button>
            </span>

</template>
<script>
import ghTable from '@/components/table'
export default {
  components: { ghTable },
  data () {
    return {
      searchForm: {
        approved: ''
      },
      dialog: {
        show: false,
        companyUuid: '',
        companyName: 'test',
        approved: 1,
        form: {
          account: '',
          secretKey: '',
          note: ''
        }
      }
    }
  },
  methods: {
    reloadTable () {
      console.log(this.searchForm)
      this.$nextTick(() => {
        this.$refs.ghTable.reloadTableList()
      })
    },
    examCompany (companyUuid, companyName, approved) {
      Object.assign(this.dialog.form, { account: '', secretKey: '', note: '' })
      Object.assign(this.dialog, { companyName, companyUuid, approved, show: true })
    },
    examSubmit () {
      let url = '/company/checkCompany'
      let { companyUuid: uuid, approved } = this.dialog
      this.$ajax.post(url, Object.assign({}, this.dialog.form, { uuid, approved }))
        .then(() => {
          this.$refs.ghTable.reloadTableList()
          this.$message.success('提交成功')
          this.dialog.show = false
        })
        .catch((e) => {
          this.$message.error(e.msg || '服务器繁忙,提交失败!')
        })
    },
    delCompany (uuid, companyName) {
      this.$confirm(`确认删除${companyName}？`)
        .then((res) => {
          console.log(res)
          this.$ajax.post('/company/deleteByUuid', { uuid })
        })
        .then(() => {
          this.$refs.ghTable.reloadTableList()
          this.$message.success('删除成功')
        })
        .catch((e) => {
          if (e == 'cancel') {
            return
          }
          this.$message.error(e.msg || '服务器繁忙,删除失败!')
        })
    }
  }
}
</script>
<style lang="scss">
@import "~@/styles/_variables";
.dialog-tip {
  margin-bottom: 20px;
  color: #333;
  font-size: 16px;
  &-name {
    color: $orange;
    padding: 0 5px;
  }
}
</style>
